import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import { ITableSelection } from '../types';
import selectTableSelection from './selectTableSelection';

export default function selectCols(
  controller: Controller,
  table: Table,
  startIndex: number,
  endIndex: number,
) {
  const min = Math.min(startIndex, endIndex);
  const max = Math.max(startIndex, endIndex);
  const rows = table.nodes;
  const tableSelection: ITableSelection = {
    startRowIndex: 0,
    startColIndex: min,
    endRowIndex: rows.length - 1,
    endColIndex: max,
    key: table.key,
    type: 'col',
  };
  return selectTableSelection(controller, table, tableSelection, false);
}
